@mixin clock-size {
  height: $clock-height;
  width: $clock-width;
}

@mixin positioning {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin position-absolute-half-block {
  position: absolute;
  top: 50%;
  left: 50%;
}

@mixin b-radius {
  border-radius: 50%;
}

@mixin rectangle($width, $height) {
  width: $width;
  height: $height;
}
